body {
  background-color: lightgrey;
}

/*********************************************************
 * Home
*********************************************************/
.home-header {
  font-family: fantasy;
  text-align: center;
}

.horizontal-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.vertical-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.flex-1 {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.home-tree-container {
  width: calc(100% - 150px);
}

.home-tree-container rules-tree {
  height: 60%;
}

.home-tree-container rule-tester {
  height: 40%;
}

.home-container rules-list {
  width: 150px;
}

/*********************************************************
 * Rules list
*********************************************************/
rules-list {
  height: 80vh;
  overflow-y: auto;
}

rules-list .rule-list-item {
  margin-top: 2px;
  margin-bottom: 2px;
}

/*********************************************************
 * Rules tree
*********************************************************/
rules-tree .container {
  position: relative;
}

.dragover {
  background-color: #eee;
}

.delete-rule-icon, .drag-active .delete-rule-icon.drop-container {
  background-color: red;
  width: 120px;
  height: 100px;
  position: absolute;
  left: 10px;
  bottom: 10px;
  background: url("assets/delete-button.png") no-repeat;
  background-size: cover;
  visibility: visible;
}

.delete-rule-icon.hidden, .drag-active .delete-rule-icon.drop-container.hidden {
  visibility: collapse;
}

.Treant {
  overflow-y: scroll;
}

/*********************************************************
 * Tester
*********************************************************/
.regex-box {
  width: auto;
  height: 50px;
  background-color: white;
  border-radius: 5px;
  margin: 0 10px 0 10px;
  font-size: 25px;
  font-family: monospace;
  padding: 3px 10px 0 10px;
}

.regex-box brackets {
  color: #18ca8f;
}

.regex-box multiplier {
  color: #f7cc59;
}

.tester-area {
  width: calc(100% - 20px);
  height: 100%;
  margin: 10px;
  background-color: white;
  border-radius: 5px;
}

.tester-area textarea {
  width: 100%;
  height: 100%;
  padding: 10px;
  border-color: transparent;
  font-size: 15px;
}

.tester-area-highlight {
  color: #153b63;
  background-color: #97cdf9;
}

.sa-dropdown {
  visibility: hidden;
}

/*********************************************************
 * Rules
*********************************************************/
.rule-node {
  width: 100px;
  height: 70px;
}

.rule-node .card.horizontal {
  width: 100%;
  height: 100%;
  margin: 0;
}

.card .card-content {
  padding: 0px;
  min-height: 70px;
}

.tree-rule-name {
  padding: 5px 5px;
  font-weight: 700;
  font-size: 11px;
  text-align: center;
}

.tree-rule-name.leaf {
  background-color: #cefdff;
}

.tree-rule-name.root {
  background-color: #a8c4ff;
}

.tree-rule-name.group {
  background-color: #79fbd0;
}

.tree-rule-name.decorator {
  background-color: #f7cc59;
}

.tree-rule-desc {
  padding: 5px 3px 5px 3px;
  font-size: 10px;
  line-height: 12px;
}

.tree-rule-value {
  padding: 5px 3px 5px 3px;
  font-size: 12px;
  line-height: 13px;
}

.card-content input.tree-rule-input {
  font-size: 11px;
  border: 1px solid grey;
  text-align: center;
  width: 55px;
  height: 18px;
}

.card-content .hidden {
  visibility: collapse;
  height: 0px;
}

.card-content form {
  height: 21px;
}

@media only screen and (max-width: 500px) {
  rules-tree .rule-node {
    width: 60px;
    height: 30px;
  }
  rules-tree .tree-rule-desc {
    padding: 0;
    height: 0px;
    visibility: hidden;
  }
  rules-tree .card .card-content {
    padding: 0px;
    min-height: 30px;
  }
  rules-tree .tree-rule-name {
    font-size: 9px;
  }
  rules-tree .tree-rule-value {
    font-size: 9px;
    line-height: 10px;
  }
  rules-tree .card-content input.tree-rule-input {
    width: 15px;
    height: 15px;
  }
}

